import * as React from "react";
import ImageList from "@mui/material/ImageList";
import ImageListItem from "@mui/material/ImageListItem";
import ImageListItemBar from "@mui/material/ImageListItemBar";
import IconButton from "@mui/material/IconButton";
import { API_URL } from "../../../../service/KEYS";

import DeleteIcon from '@mui/icons-material/Delete';

export default function TitlebarImageList({ image, index,deleteImage }) {
  return (
    <ImageListItem key={index}>
      <img
        src={`${API_URL}/products/images/300/${image.public_name}`}
        alt={image.original_name}
        loading="lazy"
      />
      <ImageListItemBar
        title={image.original_name}
        subtitle={image.public_name}
        position="below"
        actionIcon={
          <IconButton onClick={()=>deleteImage(image)}>
            <DeleteIcon />
          </IconButton>
        }
      />
    </ImageListItem>
  );
}
