import React,{useState} from 'react';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

export default function ColorToggleButton({available,handleChangeAvailable}) {
 
  const handleChange = (event, newAlignment) => {
    
    handleChangeAvailable(newAlignment);
  };

  return (
    <ToggleButtonGroup
      color="standard"
      value={available}
      exclusive
      onChange={handleChange}
      size='small'
      sx={{background:"#1976d2",color:"#fff",height:"30px"}}
    >
      <ToggleButton sx={{color:'#fff'}} value="">Todos</ToggleButton>
      <ToggleButton sx={{color:'#fff'}} value="/a">Publicos</ToggleButton>
      <ToggleButton sx={{color:'#fff'}} value="/na">Privados</ToggleButton>
    </ToggleButtonGroup>
  );
}
