import React, { useState, useEffect, useContext } from "react";
import { useForm, Controller } from "react-hook-form";
import TextField from "@mui/material/TextField";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import NativeSelect from "@mui/material/NativeSelect";
import Checkbox from "@mui/material/Checkbox";
import Button from "@mui/material/Button";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import Typography from '@mui/material/Typography';
import { styled } from "@mui/material/styles";
import AddPhotoAlternateIcon from "@mui/icons-material/AddPhotoAlternate";

import UserContext from "../../../../contex/user/UserContext";
import AlertGeneral from "../../../../components/general/AlertGeneral";

import {
  updateProduct,
  getNamesImage,
  getProductById,
} from "../../../../service/api/productService";
import { getCategories } from "../../../../service/api/categoryService";
import { API_URL } from "../../../../service/KEYS";
import { useHistory } from "react-router-dom";
const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const label = { inputProps: { "aria-label": "Checkbox demo" } };

const EditContent = ({ product, id }) => {
  const {
    handleSubmit,
    control,
    formState: { errors },
    reset,
  } = useForm();
  const [stateAlert, setStateAlert] = useState(false);
  const [data, setData] = useState({
    severity: "success",
    variant: "filled",
    message: "Se actualizo exitosamente!",
  });
  let history = useHistory();
  const { token } = useContext(UserContext);
  const [category, setCategory] = useState(-1);
  const [categories, setCategories] = useState([]);
  const [namesImg, setNamesImg] = useState([]);
  const [available, setAvailable] = useState(true);
  const [availablePrice, setAvailablePrice] = useState();
  const [defaultImage, setDefaultImage] = useState("");

  const handleChangeCategory = (event) => {
    setCategory(event.target.value);
  };
  const handleChangeDefaultImage = (event) => {
    console.log(event.target.value);
    setDefaultImage(event.target.value);
  };
  const availableChage = (event) => {
    setAvailable(event.target.checked);
  };
  const availablaPriceChage = (event) =>{
    setAvailablePrice(event.target.checked);
  }
  const redirectProductImages = () => {
    history.push(`/admin/edit/product/images/${product.id}`);
  };
  const onSubmit = async (data) => {
    try {
      data.category_id = category;
      data.available = available;
      data.default_image = defaultImage;
      data.price = {
        totalPrice: data.price,
        visible: availablePrice
      }
      

      const res = await updateProduct(product.id, data, token);
      product = res.product;
      setStateAlert(true);
    } catch (error) {}
  };
  useEffect(() => {
    const init = async () => {
      if (categories.length == 0) {
        const res = await getCategories(token);
        setCategories(res);
        const names = await getNamesImage(product.id, token);
        setNamesImg(names);
        setDefaultImage(product.default_image);
        setCategory(product.category_id);
      }
    };
    init();
  }, []);
  return (
    <div>
      <AlertGeneral open={stateAlert} setOpen={setStateAlert} data={data} />
      <Button
        onClick={redirectProductImages}
        variant="contained"
        endIcon={<AddPhotoAlternateIcon />}
        size="small"
      >
        Cambiar imagenes
      </Button>
      <div style={{height:'15px'}}></div>
      <form onSubmit={handleSubmit(onSubmit)}>
        
        <Grid
          component={Paper}
          container
          rowSpacing={1}
          columns={12}
          columnSpacing={{ xs: 1, sm: 2, md: 3 }}
        >
          <Grid item xs={12} >
            <Typography align="center" variant="h4" mt={2} fontWeight='bold' sx={{ color: "#004d40" }}>Actualizar producto</Typography>
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <strong>Cdigo: </strong><span>{product.code}</span>
          </Grid>
          <Grid item xs={12} sm={10} md={10} >
            <Controller
              name="name"
              control={control}
              defaultValue={product.name}
              render={({
                field: { onChange, value },
                fieldState: { error },
              }) => (
                <TextField
                  label="Nombre del producto"
                  variant="standard"
                  sx={{ width: "90%" }}
                  value={value}
                  onChange={onChange}
                  error={!!error}
                  helperText={error ? error.message : null}
                />
              )}
              rules={{
                required: true,
                minLength: 3,
                maxLength: 50,
              }}
            />
            <br />
            <span style={{ color: "red" }}>
              {errors.name?.type === "required" && "Campo obligatorio"}
            </span>
            <span style={{ color: "red" }}>
              {errors.name?.type === "minLength" && "Nombre muy corto"}
            </span>
            <span style={{ color: "red" }}>
              {errors.name?.type === "maxLength" && "Nombre demaciado largo"}
            </span>
          </Grid>

          <Grid item xs={12} sm={6} md={6} mt={2}>
            <Controller
              name="price"
              control={control}
              defaultValue={product.price_id.totalPrice}
              render={({
                field: { onChange, value },
                fieldState: { error },
              }) => (
                <TextField
                  label="Precio"
                  variant="standard"
                  sx={{ width: "90%" }}
                  value={value}
                  onChange={onChange}
                  error={!!error}
                  helperText={error ? error.message : null}
                  type="number"
                />
              )}
              
            />
    
          </Grid>
          <Grid item xs={12} sm={6} md={6} mt={2}>
            <br />
            Mostrar precio{" "}
            <Checkbox
              value={availablePrice}
              onChange={availablaPriceChage}
              {...label}
              defaultChecked={product.price_id.visible}
              color="success"
            />
          </Grid>

          <Grid item xs={12} sm={6} md={6} mt={2}>
            <FormControl variant="standard" defa sx={{ m: 1, width: "90%" }}>
              <InputLabel id="demo-simple-select-standard-label">
                Categoria
              </InputLabel>
              <NativeSelect
                labelId="demo-simple-select-standard-label"
                id="demo-simple-select-standard"
                displayEmpty
                value={category}
                onChange={handleChangeCategory}
                label="Categoria"
                defaultValue={product.category_id}
              >
                {categories.map((item) => (
                  <option key={item.id} value={item.id}>
                    {item.name}
                  </option>
                ))}
              </NativeSelect>
            </FormControl>
            <br />
          </Grid>

          <Grid item xs={12} sm={6} md={6} mt={2}>
            <br />
            Publicar{" "}
            <Checkbox
              value={available}
              onChange={availableChage}
              {...label}
              defaultChecked={product.available}
              color="success"
            />
          </Grid>

          <Grid item xs={12} sm={12} md={12} mt={2}>
            <Controller
              name="description"
              control={control}
              defaultValue={product.description}
              render={({
                field: { onChange, value },
                fieldState: { error },
              }) => (
                <TextField
                  label="Descripcin"
                  variant="standard"
                  multiline
                  rows={4}
                  sx={{ width: "90%" }}
                  value={value}
                  onChange={onChange}
                  error={!!error}
                  helperText={error ? error.message : null}
                />
              )}
              rules={{
                required: true,
                minLength: 3,
              }}
            />
            <br />
            <span style={{ color: "red" }}>
              {errors.name?.type === "required" && "Campo obligatorio"}
            </span>
            <span style={{ color: "red" }}>
              {errors.name?.type === "minLength" && "Descripcin muy corta"}
            </span>
          </Grid>
          <Grid item xs={12} sm={6} md={6} mt={2}>
            <FormControl variant="standard" defa sx={{ m: 1, width: "90%" }}>
              <InputLabel id="demo-simple-select-standard-label">
                Imagen principal
              </InputLabel>
              <NativeSelect
                labelId="demo-simple-select-standard-label"
                id="demo-simple-select-standard"
                displayEmpty
                value={defaultImage}
                onChange={handleChangeDefaultImage}
                label="Categoria"
                defaultValue={defaultImage}
              >
                {namesImg.map((image) => (
                  <option key={image.id} value={image.public_name}>
                    {image.original_name}
                  </option>
                ))}
              </NativeSelect>
            </FormControl>
            <br />
          </Grid>
          <Grid item xs={12} sm={6} md={6} mt={2}>
            <img src={`${API_URL}/products/images/300/${defaultImage}`} />
          </Grid>

          <Grid item xs={12} sm={12} md={12}>
            <Button  type="submit" variant="contained" color="success"  >Actualizar</Button>
          </Grid>
          <Grid mt={2}>
            <Item></Item>
          </Grid>
        </Grid>
      </form>
    </div>
  );
};

export default EditContent;
