import React, { useEffect, useState, useContext } from "react";
import { useParams, useHistory } from "react-router-dom";
import UserContext from "../../../../contex/user/UserContext";
import AlertGeneral from "../../../../components/general/AlertGeneral";
import SaveIcon from "@mui/icons-material/Save";
import AddPhotoAlternateIcon from "@mui/icons-material/AddPhotoAlternate";

import Paper from "@mui/material/Paper";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import EditIcon from "@mui/icons-material/Edit";
import Grid from "@mui/material/Grid";


import ListImages from "../../../../components/general/ListImages";


import {
  getServiceById,
  sendImages,
} from "../../../../service/api/serviceService";
import ListViewImg from './ListViewImg';

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));
const label = { inputProps: { "aria-label": "Checkbox demo" } };
const Input = styled("input")({
  display: "none",
});

const EditImg = () => {
  const { id } = useParams();
  let history = useHistory();
  const { token } = useContext(UserContext);
  const [service, setService] = useState({});

  const [source, setSource] = useState([]);
  const [listImages, setListImages] = useState(null);
  const [saveState, setSaveState] = useState(true);

  const [flagStateImages, setFlagStateImages] = useState(false);

  const [alertState, setAlertState] = useState(false);
  const [data, setData] = useState({
    severity: "success",
    variant: "filled",
    message: "Se guardo exitosamente!",
  });

  const changeImages = (event) => {
    var images = event.target.files;
    setListImages(images);
    var list = [];
    if (images) {
      for (let index = 0; index < images.length; index++) {
        list.push(URL.createObjectURL(images[index]));
      }
      setSaveState(false);
    } else {
      setSaveState(true);
    }
    setSource(list);
  };

  const sendImagesNews = async () => {
    try {
      const formData = new FormData();
      if (listImages) {
        for (let index = 0; index < listImages.length; index++) {
          formData.append(`files`, listImages[index]);
        }
      }
      try {
        const otroRes = await sendImages(id, formData, token);
      } catch (error) {}
      setFlagStateImages(!flagStateImages);
      setSource([]);
      setSaveState(true);
      setAlertState(true);
    } catch (error) {
      setData({
        ...data,
        severity: "error",
        message: "Ocurrio un error de respuesta.",
      });
      setAlertState(true);
    }
  };
  const editService = async () => {
    history.push({
      pathname: `/admin/edit/service/${service.id}`,
      state: service,
    });
  };
  useEffect(() => {
    const init = async () => {
      try {
        const res = await getServiceById(id, token);
        setService(res);
      } catch (error) {}
    };
    init();
  }, [flagStateImages]);
  return (
    <div>
      <AlertGeneral open={alertState} setOpen={setAlertState} data={data} />
     <form>
     <Grid
          component={Paper}
          container
          rowSpacing={1}
          columns={12}
          columnSpacing={{ xs: 1, sm: 2, md: 3, mt: 2 }}
        >
        <Grid item xs={6} sm={6} md={6}  mt={2}>
        <label htmlFor="contained-button-file">
          <Input
            accept="image/*"
            id="contained-button-file"
            multiple
            type="file"
            onChange={changeImages}
          />
          <Button variant="contained" color="secondary" size="small" component="span" startIcon={<AddPhotoAlternateIcon/>}>
            Subir imagenes
          </Button>
        </label>
        </Grid>
        <Grid item xs={6} sm={6} md={6} mt={2}>
          <Button
           sx={{ mx: "auto" }}
          size="small"
          onClick={editService}
          variant="contained"
          color="success"
          startIcon={<EditIcon />}
        >
          Editar service
        </Button></Grid>
     
        
        <Grid mt={2} padding={2}><ListImages source={source}/></Grid>
        
        <div style={{ height: "15px" }}></div>
        <Grid mt={2} item xs={12} sm={12} md={12}>
          <Button
          disabled={saveState}
          onClick={sendImagesNews}
          variant="contained"
          startIcon={<SaveIcon />}
        >
          Guardar
        </Button>
        </Grid>
        <div style={{ height: "15px" }}></div>
        <Grid mt={2} item xs={12} sm={6} md={6}>
         
        </Grid>
        </Grid>
      </form> 
      <div style={{ height: "15px" }}></div>
      <ListViewImg
        id={id}
        token={token}
        service = {service}
        flagStateImages={flagStateImages}
      />
    </div>
  );
};

export default EditImg;
