import React,{useState,useContext} from "react";
import { useForm, Controller } from "react-hook-form";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import { styled } from "@mui/material/styles";
import Typography from '@mui/material/Typography';

import UserContext from "../../../../contex/user/UserContext";

import {postUser} from "../../../../service/api/userService";

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const RegisterUser = () => {
  const { token } = useContext(UserContext);
  const {
    handleSubmit,
    control,
    formState: { errors },
    reset,
  } = useForm();
  const onSubmit = (data) => {
    try {
      const res = postUser(data,token);
    } catch (error) {
      
    }
  };
  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Grid
        component={Paper}
        container
        rowSpacing={1}
        columns={12}
        columnSpacing={{ xs: 1, sm: 2, md: 3 }}
      >
         <Grid item xs={12} >
            <Typography align="center" variant="h4" mt={2} fontWeight='bold' sx={{ color: "#004d40" }}>Registrar Usuario</Typography>
          </Grid>
        <Grid item xs={12} sm={6} md={6} mt={2}>
          <Controller
            name="name"
            control={control}
            defaultValue=""
            render={({ field: { onChange, value }, fieldState: { error } }) => (
              <TextField
                label="Nombre(s)"
                variant="standard"
                sx={{width:"90%"}}
                value={value}
                onChange={onChange}
                error={!!error}
                helperText={error ? error.message : null}
              />
            )}
            rules={{
              required: true,
              pattern: /^[A-Za-z ]+$/i,
              minLength: 3,
              maxLength: 20,
            }}
          />
          <br />
          <span style={{ color: "red" }}>
            {errors.name?.type === "required" && "Campo obligatorio"}
          </span>
          <span style={{ color: "red" }}>
            {errors.name?.type === "pattern" && "Nombre invalido"}
          </span>
          <span style={{ color: "red" }}>
            {errors.name?.type === "minLength" && "Nombre muy corto"}
          </span>
          <span style={{ color: "red" }}>
            {errors.name?.type === "maxLength" && "Nombre demaciado largo"}
          </span>
        </Grid>

        <Grid item xs={12} sm={6} md={6} mt={2}>
          <Controller
            name="last_name"
            control={control}
            defaultValue=""
            render={({ field: { onChange, value }, fieldState: { error } }) => (
              <TextField
                label="Apellido(s)"
                variant="standard"
                sx={{width:"90%"}}
                value={value}
                onChange={onChange}
                error={!!error}
                helperText={error ? error.message : null}
              />
            )}
            rules={{
              required: true,
              pattern: /^[A-Za-z ]+$/i,
              minLength: 3,
              maxLength: 20,
            }}
          />
          <br />
          <span style={{ color: "red" }}>
            {errors.last_name?.type === "required" && "Campo obligatorio"}
          </span>
          <span style={{ color: "red" }}>
            {errors.last_name?.type === "pattern" && "Apellidos invalido"}
          </span>
          <span style={{ color: "red" }}>
            {errors.last_name?.type === "minLength" && "Apellidos muy corto"}
          </span>
          <span style={{ color: "red" }}>
            {errors.last_name?.type === "maxLength" &&
              "Apellidos demaciado largo"}
          </span>
        </Grid>

        <Grid item xs={12} sm={6} md={6} mt={2}>
          <Controller
            name="email"
            control={control}
            defaultValue=""
            render={({ field: { onChange, value }, fieldState: { error } }) => (
              <TextField
                label="Email"
                variant="standard"
                sx={{width:"90%"}}
                value={value}
                onChange={onChange}
                error={!!error}
                helperText={error ? error.message : null}
                type="email"
              />
            )}
            rules={{
              required: true,
              pattern:
                /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            }}
          />
          <br></br>
          <span style={{ color: "red" }}>
            {errors.email?.type === "required" && "Email obligatorio"}
          </span>
          <span style={{ color: "red" }}>
            {errors.email?.type === "pattern" && "Email invalido"}
          </span>
        </Grid>
        <Grid item xs={12} sm={6} md={6} mt={2}>
          <Controller
            name="username"
            control={control}
            defaultValue=""
            render={({ field: { onChange, value }, fieldState: { error } }) => (
              <TextField
                label="Nombre de usuario"
                variant="standard"
                sx={{width:"90%"}}
                value={value}
                onChange={onChange}
                error={!!error}
                helperText={error ? error.message : null}
              />
            )}
            rules={{
              required: true,
              minLength: 3,
              maxLength: 20,
            }}
          />
          <br />
          <span style={{ color: "red" }}>
            {errors.name?.type === "required" && "Campo obligatorio"}
          </span>
          <span style={{ color: "red" }}>
            {errors.name?.type === "minLength" && "Nombre de usuario muy corto"}
          </span>
          <span style={{ color: "red" }}>
            {errors.name?.type === "maxLength" && "Nombre de usuario demaciado largo"}
          </span>
        </Grid>
        <Grid item xs={12} sm={6} md={6} mt={2}>
          <Controller
            name="password"
            control={control}
            defaultValue=""
            render={({ field: { onChange, value }, fieldState: { error } }) => (
              <TextField
                label="Contrasea"
                variant="standard"
                sx={{width:"90%"}}
                value={value}
                onChange={onChange}
                error={!!error}
                helperText={error ? error.message : null}
                type="password"
              />
            )}
            rules={{ required: true }}
          />
          <br></br>
          <span style={{ color: "red" }}>
            {errors.password?.type === "required" && "Ingrese contrasea"}
          </span>
        </Grid>
        <Grid item xs={12} sm={12} md={12} mt={2} textAlign="center">
          <Button type="submit" variant="contained" color="success">Registrar</Button>
        </Grid>
        <Grid mt={2}>
            <Item></Item>
      </Grid>
      </Grid>
      
    </form>
  );
};

export default RegisterUser;
