import React, { useState, useEffect, useContext, Fragment } from "react";
import { useForm, Controller } from "react-hook-form";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import { styled } from "@mui/material/styles";

import AlertGeneral from "../../../components/general/AlertGeneral";

import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import NativeSelect from "@mui/material/NativeSelect";
import Checkbox from "@mui/material/Checkbox";
import CollectionsIcon from "@mui/icons-material/Collections";

import UserContext from "../../../contex/user/UserContext";

import { getTypeService } from "../../../service/api/typeserviceService";
import { postService, sendImages } from "../../../service/api/serviceService";
import ListImages from "../../../components/general/ListImages";
import { padding } from "@mui/system";


const label = { inputProps: { "aria-label": "Checkbox demo" } };
const Input = styled("input")({
  display: "none",
});

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

const register = () => {
  const { token } = useContext(UserContext);
  const {
    handleSubmit,
    control,
    formState: { errors },
    reset,
  } = useForm();
  const [category, setCategory] = useState(1);
  const [categories, setCategories] = useState([]);
  const [errorCategory, setErrorCategory] = useState("");

  const [available, setAvailable] = useState(true);
  const [availablePrice, setAvailablePrice] = useState(false);

  const [images, setImages] = useState(null);
  const [source, setSource] = useState([]);

  const [saveDisable, setSaveDisable] = useState(false);

  const [alertState, setAlertState] = useState(false);
  const [data, setData] = useState({
    severity: "success",
    variant: "filled",
    message: "",
  });

  const handleChangeCategory = (event) => {
    setCategory(event.target.value);
  };
  const availableChage = (event) => {
    setAvailable(event.target.checked);
  };
  const availablePriceChage = (event) => {
    setAvailablePrice(event.target.checked);
  };
  const onSubmit = async (data) => {
    setSaveDisable(true);
    try {
      data.typeService_id = category;
      data.available = available;
      data.price = {
        totalPrice: data.price,
        visible: availablePrice,
      };
      const formData = new FormData();
      if (images) {
        for (let index = 0; index < images.length; index++) {
          formData.append(`files`, images[index]);
        }
      }

      if (!category) {
        setErrorCategory("Seleccione categoria");
      } else {
        console.log(data);
        const res = await postService(data, token);
        const otroRes = await sendImages(res.service.id, formData, token);
        setErrorCategory("");
      }
      setSaveDisable(false);
      setSource([]);
      setData({ ...data, message: "El servicio se registro exitosamente!" });
      setAlertState(true);
    } catch (error) {
      setData({
        ...data,
        message:
          "Algo salio mal por favor verifique si se registro el servicio",
        severity: "error",
      });
      setAlertState(true);
    }
  };
  
  const changeImages = (event) => {
    var images = event.target.files;
    var list = [];
    setImages(images);
    if (images) {
      for (let index = 0; index < images.length; index++) {
        list.push(URL.createObjectURL(images[index]));
      }
    }
    setSource(list);
  };

  useEffect(() => {
    const init = async () => {
      if (categories.length == 0) {
        const res = await getTypeService(token);
        setCategories(res);
      }
    };
    init();
  }, []);
  return (
    <div>
      <AlertGeneral open={alertState} setOpen={setAlertState} data={data} />
      <form onSubmit={handleSubmit(onSubmit)}>
        <Grid
          component={Paper}
          container
          rowSpacing={1}
          columns={12}
          columnSpacing={{ xs: 1, sm: 2, md: 3, mt: 2 }}
        >
          <Grid item xs={12}>
            <Typography
              align="center"
              variant="h4"
              mt={2}
              fontWeight="bold"
              sx={{ color: "#004d40" }}
            >
              Registrar Servicio
            </Typography>
          </Grid>
          <Grid item xs={12} sm={12} md={12} mt={2}>
            <Controller
              name="name"
              control={control}
              defaultValue=""
              render={({
                field: { onChange, value },
                fieldState: { error },
              }) => (
                <TextField
                  label="Nombre del servicio"
                  variant="standard"
                  sx={{ width: "90%" }}
                  value={value}
                  onChange={onChange}
                  error={!!error}
                  helperText={error ? error.message : null}
                />
              )}
              rules={{
                required: true,
                minLength: 3,
                maxLength: 50,
              }}
            />
            <br />
            <span style={{ color: "red" }}>
              {errors.name?.type === "required" && "Campo obligatorio"}
            </span>
            <span style={{ color: "red" }}>
              {errors.name?.type === "minLength" && "Nombre muy corto"}
            </span>
            <span style={{ color: "red" }}>
              {errors.name?.type === "maxLength" && "Nombre demaciado largo"}
            </span>
          </Grid>

          <Grid item xs={12} sm={6} md={6} mt={2}>
            <Controller
              name="price"
              control={control}
              defaultValue=""
              render={({
                field: { onChange, value },
                fieldState: { error },
              }) => (
                <div style={{ display: "flex" }}>
                  <div
                    id="bs"
                    style={{
                      borderRadius: "40px",
                      color: "#fff",
                      width: "40px",
                      padding: "8px",
                      textAlign: "center",
                      background: "#2e7d32",
                      marginTop: "10px",
                    }}
                  >
                    BS.
                  </div>

                  <div
                    style={{
                      marginBottom: "-70px",
                      right: "-10px",
                      marginInline: "12px",
                    }}
                  >
                    <TextField
                      label="Precio"
                      variant="standard"
                      /*                   sx={{ width: "90%" }}
                       */ value={value}
                      onChange={onChange}
                      error={!!error}
                      helperText={error ? error.message : null}
                      type="number"
                      style={{ padding: "8px" }}
                    />
                  </div>
                </div>
              )}
            />
            <br></br>
          </Grid>
          <Grid item xs={12} sm={6} md={6} mt={2}>
            <br />
            Mostrar precio{" "}
            <Checkbox
              value={availablePrice}
              onChange={availablePriceChage}
              {...label}
              defaultChecked={availablePrice}
              color="success"
            />
          </Grid>

          <Grid item xs={12} sm={6} md={6} mt={2}>
            <FormControl variant="standard" defa sx={{ m: 1, width: "90%" }}>
              <InputLabel id="demo-simple-select-standard-label">
               
              </InputLabel>
              <NativeSelect
                labelId="demo-simple-select-standard-label"
                id="demo-simple-select-standard"
                displayEmpty
                value={category}
                onChange={handleChangeCategory}
                label="Servicios"
                defaultValue={1}
              >
                {categories.map((item) => (
                  <option key={item.id} value={item.id}>
                    {item.name}
                  </option>
                ))}
              </NativeSelect>
            </FormControl>
            <br />
            <span style={{ color: "red" }}>{errorCategory}</span>
          </Grid>

          <Grid item xs={12} sm={6} md={6} mt={2}>
            <br />
            Publicar{" "}
            <Checkbox
              value={available}
              onChange={availableChage}
              {...label}
              defaultChecked
              color="success"
            />
          </Grid>

          <Grid item xs={12} sm={12} md={12} mt={2}>
            <Controller
              name="description"
              control={control}
              defaultValue=""
              render={({
                field: { onChange, value },
                fieldState: { error },
              }) => (
                <TextField
                  label="Descripcin"
                  variant="standard"
                  multiline
                  rows={4}
                  sx={{ width: "90%" }}
                  value={value}
                  onChange={onChange}
                  error={!!error}
                  helperText={error ? error.message : null}
                />
              )}
              rules={{
                required: true,
                minLength: 3,
              }}
            />
            <br />
            <span style={{ color: "red" }}>
              {errors.name?.type === "required" && "Campo obligatorio"}
            </span>
            <span style={{ color: "red" }}>
              {errors.name?.type === "minLength" && "Descripcin muy corta"}
            </span>
          </Grid>
          <Grid item xs={12} sm={12} md={12} mt={2}>
            <label htmlFor="contained-button-file">
            
              <Input
                accept="image/*"
                id="contained-button-file"
                multiple
                type="file"
                onChange={changeImages}
              />
             
              <Button
                variant="outlined"
                startIcon={<CollectionsIcon />}
                component="span"
              >
                Subir imagenes
              </Button>
            </label>
            
          </Grid>
          <Grid item xs={12} sm={12} md={12} mt={2} textAlign="center">
            <Button
              disabled={saveDisable}
              type="submit"
              variant="contained"
              color="success"
            >
              Registrar Servicio
            </Button>
          </Grid>

          <Grid mt={2}>
            <Item></Item>
          </Grid>
        </Grid>
      </form>
      <Grid mt={2}>
      <ListImages source={source} spacing={2} />
      </Grid>
    </div>
  );
};

export default register;
