import React, { useState, useEffect, useContext } from "react";
import { useParams } from "react-router-dom";
import TableGeneral from "./TableGeneral";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import Button from "@mui/material/Button";
import BtnGroupAvailable from "./BtnGroupAvailable";

import UserContext from "../../../contex/user/UserContext";
import { useHistory } from "react-router-dom";

import Grid from "@mui/material/Grid";

const ListProducts = (props) => {
  let history = useHistory();
  let { category } = useParams();
  const { token } = useContext(UserContext);
  const [available, setAvailable] = useState("");
  const handleChangeAvailable = (newAlignment) => {
    setAvailable(newAlignment);
  };
  const redirectNeProduct = () => {
    history.push("/admin/register/product");
  };
  return (
    <div>
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <Grid container columns={12} columnSpacing={{ xs: 1, dm: 2,}}>
          <Grid item xs={12} md={6}>
            <Button
              onClick={redirectNeProduct}
              variant="contained"
              endIcon={<AddCircleOutlineIcon />}
              size="small"
            >
              Nuevo Producto
            </Button>
          </Grid>
          <Grid item xs={12} md={6} align="right" mt={2}>
            <BtnGroupAvailable
              available={available}
              handleChangeAvailable={handleChangeAvailable}
            />
          </Grid>
        </Grid>
      </div>
      <div style={{ height: "15px" }}></div>
      <TableGeneral category={category} available={available} token={token} />
    </div>
  );
};

export default ListProducts;
