import React from "react";
import { Route, Switch } from "react-router-dom";
import RegisterUser from "../views/admin/pages/register/RegisterUser";
import RegisterProduct from "../views/admin/products/RegisterProduct";
import ListProducts from "../views/admin/products/ListProducts";
import EditProduct from "../views/admin/products/edit/EditProduct";
import EditService from "../views/admin/servicesdir/edit/EditService";
import EditImg from "../views/admin/products/edit/EditImg";
import ListUser from "../views/admin/users/ListUsers";
import Dashboard from "../views/admin/dashboard/Dashboard";

import RegisterService from "../views/admin/servicesdir/RegisterService";
import ListService from "../views/admin/servicesdir/ListServices";
import EditImgService from "../views/admin/servicesdir/edit/EditImg";

const AdminRoutes = () => {
  return (
    <div>
      <Switch>
        <Route
          path="/admin/welcome"
          name="AdminWelcome"
          component={Dashboard}
        />
        
        <Route
          path="/admin/register/product"
          name="RegisterProduct"
          component={RegisterProduct}
        />
        <Route
          exact
          path="/admin/list/:category"
          name="Lista"
          component={ListProducts}
        />
        <Route
          exact
          path="/admin/edit/product/:id"
          name="EditProduct"
          component={EditProduct}
        />
        <Route
          exact
          path="/admin/edit/product/images/:id"
          name="EditImg"
          component={EditImg}
        />

        <Route exact path="/admin/users" name="UserList" component={ListUser} />
        {/* SERVICE */}
        <Route
          exact
          path="/admin/register/service"
          name="RegisterService"
          component={RegisterService}
        />
        <Route
          exact
          path="/admin/list/service/:typeService"
          name="Lista service"
          component={ListService}
        />
        <Route
          exact
          path="/admin/edit/service/:id"
          name="EditService"
          component={EditService}
        />
        <Route
          exact
          path="/admin/edit/service/images/:id"
          name="EditImgService"
          component={EditImgService}
        />


      {/* USERS */}
      <Route
          path="/admin/register/user"
          name="Register"
          component={RegisterUser}
        />
      <Route
          path="/admin/users"
          name="Register"
          component={ListUser}
        />
      </Switch>
    </div>
  );
};

export default AdminRoutes;
