import React, { useState, useEffect } from "react";
import CardImage from "./EditImgCard";
import {
  deleteImageByPublicname,
  getNamesImage,
} from "../../../../service/api/productService";
import Grid from "@mui/material/Grid";
import AlertGeneral from "../../../../components/general/AlertGeneral";
import DialogAlert from "../../../../components/general/DialogAlert";

const EditImgView = ({ id, token, product, flagStateImages }) => {
  const [images, setImages] = useState([]);
  const [updateImages, setUpdateImages] = useState(false);
  const [publicname,setPublicName] = useState("");

  const [alertState, setAlertState] = useState(false);
  const [data, setData] = useState({
    severity: "info",
    variant: "filled",
    message: "",
  });

  const [alertDialogState, setAlertDialogState] = useState(false);
  const [alertDialogContent, setAlertDialogContent] = useState({
    title: "Esta seguro de elminar?",
    description: "",
    nameBtn: "Eliminar",
  });
  const clickAcceptAlertDialog = async () => {
    console.log(publicname)
    try {
      const res = await deleteImageByPublicname(publicname, token);
      setUpdateImages(!updateImages);
      
    } catch (error) {}
    handleClose();
  };
  const handleClose = () => {
    setAlertDialogState(false);
  };

  const deleteImage = (image) => {
    if (image.public_name !== product.default_image) {
      setAlertDialogContent({...alertDialogContent,description:`La imagen ${image.original_name} se eliminara definitivamente.`});
      setPublicName(image.public_name);
      setAlertDialogState(true);
    } else {
      setData({...data,message:`${image.original_name} es la imagen principal, para poder eliminar cambie su imagen principal.`});
      setPublicName("");
      setAlertState(true);
    }
  };
  useEffect(() => {
    const init = async () => {
      try {
        const res2 = await getNamesImage(id, token);
        setImages(res2);
      } catch (error) {}
    };
    init();
  }, [updateImages,flagStateImages]);
  return (
    <Grid container columns={12} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
      <DialogAlert
        open={alertDialogState}
        handleClose={handleClose}
        alertDialogContent={alertDialogContent}
        clickAcceptAlertDialog={clickAcceptAlertDialog}
      />
      <AlertGeneral open={alertState} setOpen={setAlertState} data={data} />
      {images.map((image, index) => (
        <Grid item xs={12} align="center" sm={6} md={6}>
          <CardImage index={index} image={image} deleteImage={deleteImage} />
        </Grid>
      ))}
    </Grid>
  );
};

export default EditImgView;
