import React, { useState, useEffect } from "react";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";

import { useHistory } from "react-router-dom";
import DateCustomer from "../../../components/general/DateCustomer";


import PublicIcon from "@mui/icons-material/Public";
import VpnLockIcon from "@mui/icons-material/VpnLock";

import DialogAlert from "../../../components/general/DialogAlert";

import ActionsOption from "./ActionsOption";

import {
  allServiceByType,
  deleteServiceById,
} from "../../../service/api/serviceService";
import CircularProgress from "@mui/material/CircularProgress";

const columns = [
  { id: "name", label: "Nombre", minWidth: 170 },
  { id: "code", label: "Cdigo", minWidth: 100},
  { id: "price", label: "Precio", minWidth: 100 },
  { id: "available", label: "Publico", minWidth: 50 },
  { id: "created_ad", label: "Fecha de creacin", minWidth: 100 },
  { id: "accion", label: "Acciones", minWidth: 100 },
];

export default function TableGeneral({ category, available, token }) {
  
  const [page, setPage] = useState(0);
  //lleva la cuenta de los productos
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [listServices, setListService] = useState([]);
  const [totalPage, setTotalPage] = useState(0);
  const [totalServices, setTotalService] = useState(0);

  const [alertDialogState, setAlertDialogState] = useState(false);
  const [preDeleteService, setPreDeleteService] = useState({});
  const [flagDeleteService, setFlagDeleteService] = useState(false);

  const [alertDialogContent, setAlertDialogContent] = useState({
    title: "Esta seguro de elminar?",
    description: "",
    nameBtn: "Eliminar",
  });
  const clickAcceptAlertDialog = async () => {
    try {
      const res = await deleteServiceById(preDeleteService.id, token);
      setFlagDeleteService(!flagDeleteService);
      handleClose();
    } catch (error) {}
  };
  const handleClose = () => {
    setAlertDialogState(false);
  };
  
  const handleChangePage = async (event, newPage) => {
    try {
      if (newPage <= totalPage) {
        setPage(newPage);
        const res = await allServiceByType(
          category,
          token,
          available,
          newPage,
          rowsPerPage
        );
        setListService(res.content);
        setTotalPage(res.totalPages);
        setTotalService(res.totalServices);
      }
    } catch (error) {}
  };
  const deleteService = async (service) => {
    setAlertDialogState(true);
    setPreDeleteService(service);
    setAlertDialogContent({
      ...alertDialogContent,
      description: `El servicio ${service.name} se eliminara definitivamente.`,
    });
  };
  
  const handleChangeRowsPerPage = async (event) => {
    setRowsPerPage(+event.target.value);
    setFlagDeleteService(!flagDeleteService);
  };

  useEffect(() => {
    const init = async () => {
      try {
        const res = await allServiceByType(
          category,
          token,
          available,
          0,
          rowsPerPage
        );
        console.log(res)
        setListService(res.content);
        setTotalPage(res.totalPages);
        setTotalService(res.totalServices);
      
      } catch (error) {}
    };
    init();
  }, [category, flagDeleteService, available]);
  return (
    <Paper sx={{ width: "100%", overflow: "hidden" }}>
      <TableContainer sx={{ maxHeight: 700 }}>
        <Table size="small" stickyHeader aria-label="customized table">
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  sx={{
                    backgroundColor: "#004d40",
                    color: "#ffff",
                    height: "50px",
                  }}
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {!listServices && <CircularProgress />}
            {listServices.map((row) => {
              return (
                <TableRow hover={true} key={row.code}>
                  <TableCell>{row.name}</TableCell>
                  <TableCell>{row.code}</TableCell>
                  {row.price_id.visible&&<TableCell sx={{ color: "#2980b9" }} >{row.price_id.totalPrice} Bs.</TableCell>}
                  {!row.price_id.visible&&<TableCell >{row.price_id.totalPrice} Bs.</TableCell>}
                  <TableCell>
                    {row.available && <PublicIcon sx={{ color: "#2980b9" }} />}
                    {!row.available && <VpnLockIcon />}
                  </TableCell>
                  <TableCell><DateCustomer date={row.created_at}/></TableCell>
                  <TableCell>
                    <ActionsOption deleteService={deleteService} service={row}/>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[5, 10, 20]}
        component="div"
        count={totalServices}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
        labelRowsPerPage={"Filas por pgina:"}
        labelDisplayedRows={function defaultLabelDisplayedRows({
          from,
          to,
          count,
        }) {
          return `${from}${to} de ${count !== -1 ? count : `ms que ${to}`}`;
        }}
      />
      <DialogAlert
        open={alertDialogState}
        clickAcceptAlertDialog={clickAcceptAlertDialog}
        alertDialogContent={alertDialogContent}
        handleClose={handleClose}
      />
    </Paper>
  );
}
