import API from "../apiConection";
import { createHeader } from "../KEYS";

export async function postUser(data, token) {
  const header = createHeader(token);
  try {
    const res = await API.post("/users", data, header);
    return res.data;
  } catch (error) {
    console.log(error);
  }
}

export async function getUsers( token) {
    const header = createHeader(token);
    try {
      const res = await API.get("/users", header);
      return res.data;
    } catch (error) {
      console.log(error);
    }
  }